.admin__header {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    height: 100px;
    width: 100%;
    background-color: $color__black1;
    padding: 27px 25px 0 25px;
    justify-content: space-between;
    z-index: 5;
    &-button {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: $color__white;
    }
    &-menu{
        z-index: 4;
        width: 307px;
        position: fixed;
        top: 100px;
        height: 100%;
        left: 0;
        transform: translateX(-100%);
        transition: all 0.3s ease;
        background-color:  $color__white;
        &--active{
            transform: translateX(0%);
        }
    }
}
